import React from 'react'
import ProjectsStyle from './Projects.module.css'
import Project from '../../Container/Project/Project'
import SKCF from '../../Global/skcf.gif'
import RING from '../../Global/ring_election.gif'

const Projects = props=>{
    return(
        <div className={ProjectsStyle.Block}>
            <Project src = {SKCF} intro = {intros.skcf} link="https://github.com/lvhaolin1993/sKCF_improvements"></Project>
            <Project src = {RING} intro = {intros.ring} link="https://github.com/lvhaolin1993/Distributed_Election"></Project>
        </div>
    )
}

const intros={
    skcf:"An effective object tracking solution based on the Scalable Kernelized Correlation Filter (sKCF) framework. A long-term Visual Tracker to track the object position, rotation and scale change",
    ring:"Communication between a distributed system is interesting. This course project simulates several ring election algorithms which select the biggest number among distributed node"
}

export default Projects;